.forecastContainer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.9);
  transition: 0.3s all;
  /* background-color: #56a0ee; */
}
.forecastContainer .day {
  font-size: 30px;
  flex-basis: 10%;
  padding: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-row-gap: 2rem;
  row-gap: 2rem;
  transition: 0.3s all;
}
.day {
  color: #fff;
  background-color: rgba(0, 0, 0, 0.4);
  text-align: center;
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: auto;
  transition: all 2s;
  margin: 1px;
}
.forecastIcon {
  width: 130px;
  position: absolute;
}
.lightForCon {
  background: rgba(255, 255, 255, 0.4);
  color: #000;
}
.light {
  color: #000;
}
