.searchList {
  position: absolute;
  margin: 0;
  margin-left: 6px;
  width: 210px;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
  box-shadow: 1px 10px 15px 2px rgba(0, 0, 0, 0.5);
  background-color: rgba(123, 131, 139, 0.9);
  z-index: 1;
}
li {
  list-style: none;
  padding-top: 10px;
  padding-bottom: 10px;
  margin-left: -40px;
  padding-left: 15px;
}
ul li:last-child:hover {
  background: rgba(0, 0, 0, 0.25);
  color: #fff;
  border-bottom-right-radius: 25px;
  border-bottom-left-radius: 25px;

  transition: all 0.1s;
}
li:hover {
  transition: all 0.3s;
  background: rgba(0, 0, 0, 0.25);
  color: #fff;
  cursor: pointer;
}
.mainIconWeather {
  width: 220px;
  position: relative;
  top: -80px;
}

.typographyMainTemp {
  color: #fff;
